export default {
  data () {
    return {
      badges: [
        'data:image/png;base64,',
        'data:image/png;base64,',
        'data:image/png;base64,'
      ],
      currentBadge: ''
    }
  },
  created () {
    if (this.$themeConfig.titleBadgeIcons) {
      this.badges = this.$themeConfig.titleBadgeIcons
    }
    this.currentBadge = this.getBadge()
  },
  watch: {
    '$route.path' () {
      this.currentBadge = this.getBadge()
    }
  },
  methods: {
    getBadge () {
      return this.badges[Math.floor(Math.random() * this.badges.length)]
    }
  }
}
